import React, { useState, useEffect, useContext } from 'react';
import icons from './icons';
import getKey from './KeyGenerator';
import EditableTextField from './EditableTextField';
import CollapseToggleButton from './CollapseToggleButton';
import Selector from '../forms/FunctionalSelector';
import CircuitTimer from '../utils/CircuitTimer';
import CircuitTimerGPT from './CircuitTimerGPT';
import { CircuitContext } from '../context/CircuitContext';

const Circuit = ({

    //circuits,
    //setCircuits,
    circuitGroupIndex,
    circuitIndex,
    circuit,
    //activeIndex,
    //setActiveIndex
}) => {

    const {
        circuits,
        setCircuits,
        activeIndex,
        setActiveIndex
    } = useContext(CircuitContext);
    const [collapsed, setCollapsed] = useState(circuit.isCollapsed ?? false);
    const [editTitle, setEditTitle] = useState(false);
    const [editCircuit, setEditCircuit] = useState(false);
    const [editFeelings, setEditFeelings] = useState(false);
    const [editTodaysGoals, setEditTodaysGoals] = useState(false);
    const [editFutureGoals, setEditFutureGoals] = useState(false);
    const [editGratefulFor, setEditGratefulFor] = useState(false);

    const [editedCircuit, setEditedCircuit] = useState(null);
    const [editedCircuitTitle, setEditedCircuitTitle] = useState(null);
    const [editedFeelings, setEditedFeelings] = useState(null);
    const [editedTodaysGoals, setEditedTodaysGoals] = useState(null);
    const [editedFutureGoals, setEditedFutureGoals] = useState(null);
    const [editedGratefulFor, setEditedGratefulFor] = useState(null);
    const [edit, setEdit] = useState(false);
    //const [activated, setActivated] = useState(activeIndex);

    const templateCircuit = {
        title: 'Empty...',
        description: 'Empty...',
        circuit: 'Empty...',
        excersizes: [],
        isCollapsed: false
    }
    const times = [5, 10, 15, 20, 25, 30, 35, 40, 45, 50];

    useEffect(() => {
        const newCircuits = [...circuits];
        const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex] ?? templateCircuit;
        //console.log(`Circuit => collapsed: ${collapsed}`)
        //console.log(`Circuit => selectedNewCircuit: ${JSON.stringify(selectedNewCircuit, null, 2)}`);
        //console.log(`Circuit => selectedNewCircuit.isCollapsed: ${selectedNewCircuit.isCollapsed}`);
        selectedNewCircuit.isCollapsed = collapsed;
        const stringifiedData = JSON.stringify(newCircuits);
        //console.log(`Circuit => stringifiedData: ${stringifiedData}`);
        //console.log(`SetLocal => circuit: ${newCircuits[0].circuits[0].title}`)
        localStorage.setItem('circuitTracking', stringifiedData);
    }, [collapsed]);

    const toggleEditTitle = () => {
        const toggleTitle = (editTitle)
            ? false
            : true;
        const wasCircuitTitleEdited = (circuit.title !== editedCircuitTitle) ? true : false;
        setEditTitle(toggleTitle);
        setEditedCircuitTitle((toggleTitle) ? circuit.title : '');
        if (!toggleTitle && wasCircuitTitleEdited) {
            const newCircuits = [...circuits];
            const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
            selectedNewCircuit.title = (wasCircuitTitleEdited) ? editedCircuitTitle : selectedNewCircuit.title;
            //alert(`was edited: ${selectedNewCircuit.title}`)
            setCircuits(newCircuits);
        }
    }
    const toggleEditCircuit = () => {
        const toggleCircuit = (editCircuit)
            ? false
            : true;
        const wasCircuitEdited = (circuit.description !== editedCircuit) ? true : false;
        setEditCircuit(toggleCircuit);
        setEditedCircuit((toggleCircuit) ? circuit.description : '');
        if (!toggleCircuit && wasCircuitEdited) {
            const newCircuits = [...circuits];
            const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
            selectedNewCircuit.description = (wasCircuitEdited) ? editedCircuit : selectedNewCircuit.description;
            setCircuits(newCircuits);
        }
    }
    const toggleEditTodaysGoals = () => {
        const toggleTodaysGoals = (editTodaysGoals)
            ? false
            : true;
        const wasTodaysGoalsEdited = (circuit.todaysGoals !== editedTodaysGoals) ? true : false;
        setEditTodaysGoals(toggleTodaysGoals);
        setEditedTodaysGoals((toggleTodaysGoals) ? circuit.todaysGoals : '');
        if (!toggleTodaysGoals && wasTodaysGoalsEdited) {
            const newCircuits = [...circuits];
            const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
            selectedNewCircuit.todaysGoals = (wasTodaysGoalsEdited) ? editedTodaysGoals : selectedNewCircuit.todaysGoals;
            setCircuits(newCircuits);
        }
    }
    const deleteCircuit = () => {
        const toggle = window.confirm(`Are you sure you want to remove circuit: ${circuit.title}`)
        const removeItemByIndex = (array, index) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error('Index out of range');
            }
        };
        if (toggle) {
            const newCircuits = [...circuits];
            removeItemByIndex(newCircuits[circuitGroupIndex].circuits, circuitIndex);
            setCircuits(newCircuits);
        }
    }
    const ifUndefinedString = (value) => (value === undefined) ? 'empty...' : value;
    const ifUndefinedArray = (value) => (value === undefined) ? [] : value;

    const addGoal = (title) => {
        const newCircuits = [...circuits];
        const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
        const newGoal = prompt(`Add a ${title.toLowerCase().replace('goals', 'goal')}:`, '');
        if (title.toLowerCase().includes('today')) {
            selectedNewCircuit.todaysGoals.push([newGoal, false]);
            setCircuits(newCircuits);
        } else if (title.toLowerCase().includes('future')) {
            selectedNewCircuit.futureGoals.push([newGoal, false]);
            setCircuits(newCircuits);
        }
    }
    const toggleCheckbox = (index) => {
        const newCircuits = [...circuits];
        const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
        const complete = selectedNewCircuit.excersizes[index].complete ?? false;
        selectedNewCircuit.excersizes[index].complete = !complete;
        setCircuits(newCircuits);
    }
    const editGoal = (category, index) => {
        const newCircuits = [...circuits];
        const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
        if (category === 'todaysGoals') {
            const editTodayGoal = prompt(`Edit goal #${index + 1}:`, selectedNewCircuit.todaysGoals[index][0]);
            selectedNewCircuit.todaysGoals[index][0] = editTodayGoal;
            setCircuits(newCircuits);
        } else if (category === 'futureGoals') {
            const editFutureGoal = prompt(`Edit goal #${index + 1}:`, selectedNewCircuit.futureGoals[index][0]);
            selectedNewCircuit.futureGoals[index][0] = editFutureGoal;
            setCircuits(newCircuits);
        }
    }

    const deleteGoal = (category, index) => {
        const removeItemByIndex = (array) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error('Index out of range');
            }
        };
        const newCircuits = [...circuits];
        removeItemByIndex(newCircuits[circuitGroupIndex].circuits[circuitIndex][category]);
        setCircuits(newCircuits);
    }
    const updateCircuits = (circuitData) => {
        localStorage.setItem('circuitTracking', JSON.stringify(circuitData));
        setCircuits(circuitData);
    }

    const setExcersizeTime = (groupTitle, label, time) => {
        //alert(`groupTitle: ${groupTitle} label: ${label} time: ${time}`);
        const newCircuits = [...circuits];
        const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
        //alert(`selectedNewCircuit: ${selectedNewCircuit.title}`);
        selectedNewCircuit.time = Number(time);
        selectedNewCircuit.excersizes.map((excersize) => {
            excersize.complete = false;
            excersize.activated = false;
            excersize.currentTime = selectedNewCircuit.time;
            excersize.elapsedTime = 0;
        });
        updateCircuits(newCircuits);
    }
    const setRestTime = (groupTitle, label, time) => {
        const newCircuits = [...circuits];
        const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
        selectedNewCircuit.restTime = (time) ? time : selectedNewCircuit.restTime;
        setCircuits(newCircuits);
    }
    const onReset = (index) => {
        const newCircuits = [...circuits];
        const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
        selectedNewCircuit.excersizes[index].complete = false;
        selectedNewCircuit.excersizes[index].activated = false;
        selectedNewCircuit.excersizes[index].currentTime = selectedNewCircuit.time;
        selectedNewCircuit.excersizes[index].elapsedTime = 0;
        setCircuits(newCircuits);
    }
    const setExcersizeCurrentTime = (index) => console.log(`setExcersizeCurrentTime => index: ${index}`)

    const circuitHeader = (title) => {
        return <div className='flexContainer containerBox bg-lite centerVertical '>
            <div className='containerBox p-20 flex2Column color-yellow'>
                {title}
            </div>
            {
                (title.toLowerCase().includes('goal'))
                    ? <div className='flexContainer contentRight'>
                        <div
                            className='r-10 p-20 bg-lite button color-lite centeredContent'
                            onClick={() => addGoal(title)}
                        >
                            <div className='flexContainer'>
                                <div className='flex2Column text-outline-light size15'>
                                    {icons.plus}
                                </div>
                                <div className='flex2Column size30 ml-5'>
                                    {icons.darts}
                                </div>
                            </div>
                        </div>
                    </div>
                    : null
            }

        </div>
    }
    /*
    const setActivate = (activate, index) => {
        const newCircuits = [...circuits];
        const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
        selectedNewCircuit.excersizes[index].activated = activate;
        localStorage.setItem('circuitTracking', JSON.stringify(newCircuits));
        //updateCircuits(newCircuits);
    }
    */
    const triggerTimer = (index, groupIndex, subgroupIndex) => {
        window.scrollTo({
            top: window.scrollY + 150,
            behavior: 'smooth'
        });
        //const newCircuits = [...circuits];
        //const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
        //selectedNewCircuit.excersizes[index+1].activated = true;
        //newCircuits[circuitGroupIndex].circuits[circuitIndex].excersizes[index+1].activated = true;
        console.log(`triggerTimer => sessionindex${Number(index + 1)}groupIndex${groupIndex}subgroupIndex${subgroupIndex}`)
        //localStorage.setItem('activated', `sessionindex${Number(index+1)}groupIndex${groupIndex}subgroupIndex${subgroupIndex}`);
        setActiveIndex(`sessionindex${Number(index + 1)}groupIndex${groupIndex}subgroupIndex${subgroupIndex}`);
        //alert(`triggerNextTimer(${localStorage.getItem('activated')})`)
        //setCircuits(newCircuits);
    }
    const triggerChill = (index, groupIndex, subgroupIndex) => {
        window.scrollTo({
            top: window.scrollY + 50,
            behavior: 'smooth'
        });
        //const newCircuits = [...circuits];
        //const selectedNewCircuit = newCircuits[circuitGroupIndex].circuits[circuitIndex];
        //selectedNewCircuit.excersizes[index+1].activated = true;
        //newCircuits[circuitGroupIndex].circuits[circuitIndex].excersizes[index+1].activated = true;
        console.log(`triggerChill => chillindex${Number(index)}groupIndex${groupIndex}subgroupIndex${subgroupIndex}`)
        //localStorage.setItem('activated', `chillindex${Number(index)}groupIndex${groupIndex}subgroupIndex${subgroupIndex}`);
        setActiveIndex(`chillindex${Number(index)}groupIndex${groupIndex}subgroupIndex${subgroupIndex}`);
        //setActivated(index+1);
        //alert(`triggerNextTimer(${localStorage.getItem('activated')})`)
        //setCircuits(newCircuits);
    }
    const chillComplete = (index) => console.log(`${circuits[circuitGroupIndex].circuits[circuitIndex].excersizes[index].title} chill complete`);
    const chillTimer = (index, goal) => <CircuitTimer
        category='chill'
        excersize={goal}
        index={index}
        groupIndex={circuitGroupIndex}
        subgroupIndex={circuitIndex}
        //circuits={circuits}
        time={circuit.restTime}
        toggleCheckbox={chillComplete}
        //activated={activated}
        triggerNextTimer={triggerTimer}
    //setActivate={setActivate}
    />
    const sessionTimer = (index, goal) => <CircuitTimer
        category='session'
        excersize={goal}
        index={index}
        groupIndex={circuitGroupIndex}
        subgroupIndex={circuitIndex}
        circuits={circuits}
        time={circuits[circuitGroupIndex].circuits[circuitIndex].time}
        toggleCheckbox={toggleCheckbox}
        //activated={activated}
        triggerNextTimer={triggerChill}
    //setActivate={setActivate}
    />
    const circuitCheckBox = (index, goal, category) => {
        //console.log(`circuitCheckBox => ${goal.title}: ${goal.complete}`);
        return <div className=''>
            {
                (!edit)
                    ? <div
                        className='containerBox flexContainer bg-lite p-20 button'
                        onClick={() => toggleCheckbox(index)}
                    >
                        <input
                            className='flexColumn regular-checkbox button mr-20'
                            checked={goal.complete}
                            type='checkbox'
                            id='completed'
                            onChange={() => console.log(`category: ${category}, index: ${index}`)}
                        />
                        <div className='flexColumn' onClick={() => editGoal(category, index)}>
                            {index + 1}. {goal.title}
                        </div>
                    </div>
                    : <div
                        className='containerBox bg-lite button centeredContent'
                        onClick={() => deleteGoal(category, index)}
                    >
                        {icons.delete}
                    </div>
            }
        </div>
    }
    const circuitField = (isEdit, setEdited, edited, data, toggleEdit, category) => {
        return <div className=''>
            <div className='color-soft'>
                {
                    (isEdit)
                        ? <textarea
                            className='inputField size20 r-10 height-200 p-20 button'
                            onChange={(e) => setEdited(e.target.value)}
                            value={edited !== null ? edited : ifUndefinedArray(data)}
                            placeholder={edited}
                        >
                            {edited}
                        </textarea>
                        : (typeof data === 'string')
                            ? <div onClick={() => toggleEdit()}>
                                {ifUndefinedArray(data).split('\n').map((line, index) => (
                                    <React.Fragment key={getKey(`data${index}`)}>
                                        {line}
                                        {<br />}
                                    </React.Fragment>
                                ))}
                            </div>
                            : ifUndefinedArray(data).map((goal, index) => <div key={getKey(`goal${index}`)} className='containerBox centerVertical'>
                                {circuitCheckBox(index, goal, category)}
                                {sessionTimer(index, goal)}
                                {chillTimer(index, goal)}
                            </div>
                            )
                }
            </div>
        </div>
    }
    const timeSelectors = () => <div className='grid200'>
        <div className='flexContainer centerVertical'>
            <div className='containerBox flexContainer flex2Column'>
                <div className='containerBox flex3Column color-yellow'>
                    Session time:
                </div>
                <div className='flex1Auto color-yellow pr-10'>
                    <Selector
                        groupTitle='time'
                        label='Session time:'
                        items={times}
                        selected={circuits[circuitGroupIndex].circuits[circuitIndex].time}
                        onChange={setExcersizeTime}
                        fontSize='25'
                        padding='10px'
                        width='100%'
                    />
                </div>
            </div>
        </div>
        <div className='containerBox flexContainer flex2Column'>
            <div className='containerBox flex3Column color-yellow'>
                Chill time:
            </div>
            <div className='flex1Auto color-yellow pr-10'>
                <Selector
                    groupTitle='time'
                    label='Chill time:'
                    items={times}
                    selected={circuit.restTime}
                    onChange={setRestTime}
                    fontSize='25'
                    padding='10px'
                    width='100%'
                />
            </div>
        </div>
    </div>
    //console.log(`Circuit => circuit: ${JSON.stringify(circuit, null, 2)}`)

    return <div key={`circuit${circuitIndex}`} className='containerBox lowerBorder contentLeft'>
        <div className='containerBox bg-lite'>
            <div className='flexContainer'>
                <div className='flex1Auto contentLeft'>
                    {
                        (editTitle)
                            ? <textarea
                                className='inputField ht-55 size20 r-10 bold color-lite'
                                onChange={(e) => setEditedCircuitTitle(e.target.value)}
                                value={(editedCircuitTitle !== null) ? editedCircuitTitle : circuit.title}
                                placeholder={circuit.title}
                            >
                                {circuit.title}
                            </textarea>
                            : <div className='containerBox bg-lite centerVertical p-20 bold color-yellow'>
                                <CollapseToggleButton
                                    title={`${circuit.title}`}
                                    isCollapsed={collapsed}
                                    setCollapse={setCollapsed}
                                    align='left'
                                    editTitle={toggleEditTitle}
                                />
                            </div>
                    }
                </div>
                {
                    (editTitle)
                        ? <div
                            className={`rt-25 t-0 ml-5 mt-5 r-10 size15 button pl-20 contentRight`}
                            onClick={() => toggleEditTitle()}
                        >
                            <div className='r-10 p-10 bg-neogreen color-dark bold'>
                                save
                            </div>
                        </div>
                        : null
                }
            </div>
            {
                (collapsed)
                    ? null
                    : <EditableTextField
                        data={circuit.description}
                        toggle={toggleEditCircuit}
                        edit={editCircuit}
                        setEdited={setEditedCircuit}
                        edited={editedCircuit}
                    />
            }
            {
                (collapsed)
                    ? null
                    : <div>
                        <div className='grid200'>
                            <div className='containerBox p-25 button contentCenter'>
                                <div onClick={() => toggleEditCircuit()}>
                                    {
                                        (editCircuit)
                                            ? <div className='color-neogreen bold'>save</div>
                                            : icons.edit
                                    }
                                </div>
                            </div>
                            <div
                                className='containerBox p-25 button contentCenter'
                                onClick={() => deleteCircuit()}
                            >
                                {icons.delete}
                            </div>
                        </div>
                    </div>
            }
        </div>
        {
            (collapsed)
                ? null
                : <div>
                    {circuitHeader('Goals for today:')}
                    {timeSelectors()}
                    {circuitField(editTodaysGoals, setEditedTodaysGoals, editedTodaysGoals, circuit.excersizes, toggleEditTodaysGoals, 'excercises')}
                </div>
        }
    </div>
}
export default Circuit;