import React, { useState, useEffect, useContext } from 'react';
import CircuitGroup from './CircuitGroup';
import getKey from './KeyGenerator';
import CollapseToggleButton from './CollapseToggleButton';
//import initCircuitTracking from './initCircuitTracking';
//import initializeData from './initializeData';
import { CircuitContext } from '../context/CircuitContext';


const TrackCircuit = ({
    //circuits,
    //setCircuits,
    //targetElementRef,
    //scrollToBottom
}) => {

    console.log(`TrackCircuits => CircuitContext: ${JSON.stringify(CircuitContext.circuits,null,2)}`)
    const { 
        circuits, 
        setCircuits, 
        sort, 
        setSort 
    } = useContext(CircuitContext);
    //const [localCircuits, setLocalCircuits] = useState(initializeData('circuitTracking', initCircuitTracking));
    //const storedSort = localStorage.getItem('circuitSort');
    //const initialSort = storedSort ? storedSort === 'true' : true;
    //const [sort, setSort] = useState(initialSort);

    /*
    useEffect(() => {
        localStorage.setItem('circuitTracking', JSON.stringify(localCircuits));
    }, [localCircuits]);
    useEffect(() => {
        localStorage.setItem('circuitSort', sort);
    }, [sort]);
    const handleDeleteTask = (circuitGroupIndex, circuitIndex) => {
        const updatedCircuits = [...localCircuits];
        updatedCircuits[circuitGroupIndex].circuits.splice(circuitIndex, 1);
        setLocalCircuits(updatedCircuits);
    };
    */
    const notNull = (value) => (value !== null) ? true : false;
    const notEmpty = (value) => (value !== "") ? true : false;
    const isGood = (value) => (notNull(value) && notEmpty(value)) ? true : false;

    const addCircuit = (circuitGroupIndex, circuitIndex) => {
        const updatedCircuits = [...circuits];
        const circuitDescription = prompt('Circuit description:', '');
        //const circuit = prompt('Circuit:', '');
        const newCircuit = {
            description: circuitDescription,
            //circuit: circuit
            circuit: ''
        }
        if (isGood(circuitDescription)/* || isGood(circuit)*/) {
            updatedCircuits[circuitGroupIndex].circuits.push(newCircuit)
            setCircuits(updatedCircuits);
        }

    };
    const subtractCircuit = (circuitGroupIndex, circuitIndex) => {
        const updatedCircuits = [...circuits];
        const circuit = updatedCircuits[circuitGroupIndex].circuits[circuitIndex];
        circuit.sessions = circuit.sessions ?? [];
        circuit.isRunning = false;
        circuit.sessions.pop();
        setCircuits(updatedCircuits);
    };

    const deleteGroup = (circuitGroupIndex) => {
        const toggle = window.confirm(`Are you sure you want to remove circuit group ${circuits[circuitGroupIndex].title}`)
        const removeItemByIndex = (array, index) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error("Index out of range");
            }
        };
        if (toggle) {
            const updatedCircuits = [...circuits];
            removeItemByIndex(updatedCircuits, circuitGroupIndex);
            setCircuits(updatedCircuits);
        }
    }

    return (
        <div key={getKey('circuitGroupContainer')}>
            <div className='containerBox bg-lite centerVertical p-20 color-yellow bold size25'>
                <CollapseToggleButton
                    title={'sort'}
                    isCollapsed={sort}
                    setCollapse={setSort}
                    align='center'
                />
            </div>
            {
                (sort)
                ? circuits.map((circuitGroup, circuitGroupIndex, array) => (
                    <div key={getKey('circuitGroups')}>
                        <CircuitGroup
                            //circuits={circuits}
                            //setCircuits={setCircuits}
                            circuitGroup={circuitGroup}
                            circuitGroupIndex={(array.length - 1 - circuitGroupIndex)}
                            deleteGroup={deleteGroup}
                            addCircuit={addCircuit}
                            //setScroll={setScroll}
                            //targetElementRef={targetElementRef}
                            //scrollToBottom={scrollToBottom}
                            //sort={sort}
                        />
                    </div>
                ))
                : circuits.map((circuitGroup, circuitGroupIndex) => (
                    <div key={getKey('circuitGroups')}>
                        <CircuitGroup
                            //circuits={circuits}
                            //setCircuits={setCircuits}
                            circuitGroup={circuitGroup}
                            circuitGroupIndex={circuitGroupIndex}
                            deleteGroup={deleteGroup}
                            addCircuit={addCircuit}
                            //setScroll={setScroll}
                            //targetElementRef={targetElementRef}
                            //scrollToBottom={scrollToBottom}
                        />
                    </div>
                ))
            }
        </div>
    )
}

export default TrackCircuit