import React, { useState, useEffect, useContext, useRef } from 'react';
import icons from './icons';
import Circuit from './Circuit';
import getKey from './KeyGenerator';
import CollapseToggleButton from './CollapseToggleButton';
import initCircuitTracking from './initCircuitTracking';
import initializeData from './initializeData';
import { CircuitContext } from '../context/CircuitContext';

const CircuitGroup = ({
    //circuits,
    //setCircuits,
    circuitGroup,
    circuitGroupIndex,
    deleteGroup,
    addCircuit,
    //targetElementRef,
    //scrollToBottom,
    //sort
}) => {
    
    const { 
        circuits, 
        setCircuits,
        sort,
        targetElementRef,
        scrollToBottom 
    } = useContext(CircuitContext);

    //const [circuits, setCircuits] = useState(initializeData('circuitTracking', initCircuitTracking));
    const [collapsed, setCollapsed] = useState(circuitGroup.isCollapsed);
    const [edit, setEdit] = useState(false);
    const isEditedCircuitGroupTitle = () => (edit) ? true : false;
    const [editedCircuitGroupTitle, setEditedCircuitGroupTitle] = useState(null);
    //console.log(`CircuitGroup => circuits: ${JSON.stringify(circuits,null,2)}`)
    const [cursorPosition, setCursorPosition] = useState(0);
    const textareaRef = useRef(null);
    //const [activeIndex, setActiveIndex] = useState(localStorage.getItem('activated'));

    const focusTextarea = () => {
        if (textareaRef.current) {
            textareaRef.current.focus();
            textareaRef.current.setSelectionRange(cursorPosition, cursorPosition);
        }
    };
    useEffect(() => {
        focusTextarea();
    }, [editedCircuitGroupTitle, cursorPosition]);
    useEffect(() => {
        localStorage.setItem('circuitTracking', JSON.stringify(circuits));
    }, [circuits]);
    useEffect(() => {
        let dataUpdated = false;
        const updatedCircuits = [...circuits];
        const selectedNewCircuit = updatedCircuits[circuitGroupIndex];
        if (selectedNewCircuit.circuits === undefined) {
            selectedNewCircuit.circuits = [];
            dataUpdated = true;
        }
        if (dataUpdated) {
            setCircuits(updatedCircuits);
        }
    }, []);
    useEffect(() => {
        const updatedCircuits = [...circuits];
        const selectedCircuitGroup = updatedCircuits[circuitGroupIndex];
        selectedCircuitGroup.isCollapsed = collapsed;
        const stringifiedData = JSON.stringify(updatedCircuits);
        //console.log(`Circuit => stringifiedData: ${stringifiedData}`);
        //console.log(`SetLocal => circuit: ${updatedCircuits[0].circuits[0].title}`)
        //localStorage.setItem('circuitTracking', stringifiedData);
        //setCircuits(updatedCircuits);
    }, [collapsed]);
    const toggleEdit = () => {
        const toggle = (edit)
            ? false
            : true;
        const wasCircuitGroupTitleEdited = (circuitGroup.title !== editedCircuitGroupTitle) ? true : false;
        setEdit(toggle);
        setEditedCircuitGroupTitle((toggle) ? circuitGroup.title : '');
        if (!toggle && wasCircuitGroupTitleEdited) {
            const updatedCircuits = [...circuits];
            const selectedNewCircuitGroup = updatedCircuits[circuitGroupIndex];
            selectedNewCircuitGroup.title = (wasCircuitGroupTitleEdited) ? editedCircuitGroupTitle : selectedNewCircuitGroup.title;
            setCircuits(updatedCircuits);
        }
    }
    const handleChange = (e) => {
        setEditedCircuitGroupTitle(e.target.value)
        setCursorPosition(e.target.selectionStart);
    };
    const addToGroup = (circuitGroupIndex, elementRef) => {
        addCircuit(circuitGroupIndex)
        //setScroll(circuitGroupIndex * 50);
        scrollToBottom(elementRef);
    }
    return <div key={getKey(`circuit${circuitGroupIndex}`)} className='' ref={targetElementRef}>
        <div className=''>
            <div className='containerBox flexContainer bg-lite'>
                <div className='flex1Auto'>
                    {
                        (isEditedCircuitGroupTitle())
                            ? <div className='flexContainer'>
                                <div className='flex2Column'>
                                    <textarea
                                        ref={textareaRef}
                                        className='inputField ht-55 size25 r-10 color-yellow bold'
                                        onChange={handleChange}
                                        value={(editedCircuitGroupTitle !== null) ? editedCircuitGroupTitle : circuitGroup.title}
                                        placeholder={editedCircuitGroupTitle}
                                    >
                                    </textarea>
                                </div>
                                <div className='flexColumn'>
                                    <span>
                                        <div className='containerBox bg-neogreen color-dark bold button p-15' onClick={() => toggleEdit(circuitGroupIndex)}>save</div>
                                    </span>
                                </div>
                            </div>
                            : <div className='containerBox bg-lite centerVertical p-20 color-yellow bold size25'>
                                <CollapseToggleButton
                                    title={circuitGroup.title}
                                    isCollapsed={collapsed}
                                    setCollapse={setCollapsed}
                                    align='left'
                                    editTitle={toggleEdit}
                                />
                            </div>
                    }
                </div>
            </div>
            {
                (collapsed)
                    ? null
                    : <div className='containerBox'>
                        <div className='flexContainer contentRight'>
                            <div
                                className='containerBox flex2Column button bg-lite centeredContent'
                                onClick={() => addToGroup(circuitGroupIndex, targetElementRef)}
                            >
                                <div className='flexContainer'>
                                    <div className='flex2Column text-outline-light size20 mt-5'>
                                        {icons.plus}
                                    </div>
                                    <div className='flex2Column p-5 size25'>
                                        {icons.session}
                                    </div>
                                </div>
                            </div>
                            <div
                                className='containerBox flex2Column button bg-lite centeredContent'
                                onClick={() => deleteGroup(circuitGroupIndex)}
                            >
                                {icons.delete}
                            </div>
                        </div>
                    </div>
            }
            <div>
                {
                    (collapsed)
                        ? null
                        : (sort)
                            ? circuitGroup.circuits.map((circuit, circuitIndex, array) => (
                                <div key={getKey(`circuitContainer${circuitIndex}`)}>
                                    <Circuit
                                        //circuits={circuits}
                                        //setCircuits={setCircuits}
                                        circuitGroupIndex={circuitGroupIndex}
                                        circuitIndex={circuitIndex}
                                        circuit={circuit}
                                        //activeIndex={activeIndex}
                                        //setActiveIndex={setActiveIndex}
                                    />
                                </div>
                            ))
                            : circuitGroup.circuits.map((circuit, circuitIndex) => (
                                <div key={getKey(`circuitContainer${circuitIndex}`)}>
                                    <Circuit
                                        //circuits={circuits}
                                        //setCircuits={setCircuits}
                                        circuitGroupIndex={circuitGroupIndex}
                                        circuitIndex={circuitIndex}
                                        circuit={circuit}
                                        //activeIndex={activeIndex}
                                        //setActiveIndex={setActiveIndex}
                                    />
                                </div>
                            ))
                }
            </div>
        </div>
    </div>
}
export default CircuitGroup;